import React, { Fragment } from "react";
import DecentralizedExchangeService from "./decentralized-exc-dev-service";

const DecentralizedExchangeScreen = (props) => {
  const { db } = props;

  return (
    <Fragment>
      <DecentralizedExchangeService db={db} />
    </Fragment>
  );
};

export default DecentralizedExchangeScreen;
