import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import DecentralizedExchangeScreen from "../screens/decentralized-exchange-dev";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const DecentralizeExchangePage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Decentralized Exchange Development Company", link: "" },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <DecentralizedExchangeScreen db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Decentralized Exchange Development Services"
    headTitle="Decentralized Exchange Development Company | Justtrytech.com"
    description="Elevate your crypto experience with Justtry Technologies, a leading decentralized exchange development company. Explore secure, scalable solutions for seamless trading. Transform your vision into decentralized reality with us.
    "
    keywords="Decentralized Exchange Development, DEX Development Services, DEX Smart Contract Development, Custom Decentralized Exchange Solutions, DEX Software Development, Automated Market Maker (AMM) Development, Liquidity Pool Integration, Decentralized Finance (DeFi) Exchange  Development,  Non-Custodial Exchange Solutions, Cross-Chain DEX Development, Atomic Swaps Integration, DEX Aggregator Development, DEX User Interface (UI/UX) Design, DEX Security Audits, Token Listing Services for DEX, Cryptocurrency Wallet Integration, Decentralized Order Book Development, P2P Trading on DEX, Governance Token Integration, DEX API Development, Multi-Signature Wallet Integration, DEX Mobile App Development, Smart Contract Auditing for DEX, Cross-Platform DEX Development, Regulatory Compliance for DEX"
  ></Seo>
);

export default DecentralizeExchangePage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
